.body {
  margin: 0;
  padding: 0;
  background: #f6f6f6;
  font-family: "Poppins", sans-serif;

  height: 90vh;
  margin: auto;
  align-items: center;
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
}

img {
  margin-top: 1rem;
  position: relative;
  max-width: 100%;
  left: 5rem;
}

.app {
  background-color: #fff;
  width: 470px;
  height: 660px;
  margin: 2em auto;
  border-radius: 10px;
  padding: 1em;
  position: relative;
  overflow: hidden;
  box-shadow: 0 6px 31px -2px rgba(0, 0, 0, 0.3);
}

a {
  text-decoration: none;
  color: #257aa6;
}

p {
  margin: 1rem;
  font-size: 13px;
  color: #333;
  line-height: 2;
}
.light {
  text-align: left;
  margin-right: 2rem;
  color: #fff;
}
.light a {
  color: #fff;
}

.bg {
  width: 470px;
  height: 630px;

  background: #257aa6;
  position: absolute;
  top: -7.6em;
  left: 0;
  right: 0;
  margin: auto;
  background-image: url("https://png.pngtree.com/thumb_back/fw800/background/20190221/ourmid/pngtree-blue-creative-gradient-decoration-image_11175.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  -webkit-clip-path: ellipse(69% 46% at 56% 46%);
  clip-path: ellipse(69% 46% at 56% 46%);
}

form {
  position: relative;
  top: 7rem;
  left: 0rem;
  right: 0;
  width: 100%;
  text-align: center;
  padding: 2em;
}

header {
  width: 240px;
  height: 240px;
}

form input {
  width: 80%;
  padding: 12px 12px;
  margin: 0.7em auto;
  border-radius: 100px;
  border: none;
  background: rgb(255, 255, 255, 0.3);
  font-family: "Poppins", sans-serif;
  outline: none;
  color: #fff;
}
input::-webkit-input-placeholder {
  color: #fff;
  font-size: 13px;
}
input:-ms-input-placeholder {
  color: #fff;
  font-size: 13px;
}
input::-ms-input-placeholder {
  color: #fff;
  font-size: 13px;
}
input::placeholder {
  color: #fff;
  font-size: 13px;
}

.inputs {
  margin-top: -4em;
}

footer {
  position: relative;
  margin-top: 2rem;
  left: 0;
  right: 0;
  padding: 2em;
  text-align: center;
}

button {
  width: 100%;
  padding: 13px 15px;
  border-radius: 100px;
  border: none;
  background: #1b5ff9;
  font-family: "Poppins", sans-serif;
  outline: none;
  font-size: 1rem;
  color: #fff;
}
button:hover {
  background: #3670f7;
  transform: scale(1.01, 1.01);
}

